@import url(https://fonts.googleapis.com/css2?family=Luckiest+Guy&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  font-family: "Luckiest Guy", cursive;
}
.movedown {
  font-size: 32px;
  color: rgb(0, 0, 0);
  position: fixed;
  bottom: 4%;
  left: 50%;
  transition: color 0.2s;
}
.movedown:hover {
  color: gray;
  cursor: pointer;
}
.navbar {
  width: 100%;
  height: 50px;
  position: fixed;
  top: 0;
  padding: 10px;
}
.navbar .leftbar {
  width: 49%;
  float: left;
  margin-left: 1%;
}
.navbar .rightbar {
  width: 50%;
  float: right;
}
.navbar .rightbar li {
  display: inline-block;
  margin-left: 2%;
}
.navbar .leftbar li {
  float: left;
  display: inline-block;
}
.navbar li:hover {
  color: gray;
  text-decoration: underline;
  cursor: pointer;
}

@font-face {
  font-family: 'resume_font';
  src: url(/resume/static/media/resume_font.5b8eb60f.eot);
  src: url(/resume/static/media/resume_font.5b8eb60f.eot#iefix) format('embedded-opentype'),
       url(/resume/static/media/resume_font.d105a8a4.woff2) format('woff2'),
       url(/resume/static/media/resume_font.2188952b.woff) format('woff'),
       url(/resume/static/media/resume_font.6d8b325b.ttf) format('truetype'),
       url(/resume/static/media/resume_font.aba2cf13.svg#resume_font) format('svg');
  font-weight: normal;
  font-style: normal;
}
 
 [class^="rsm-"]:before, [class*=" rsm-"]:before {
  font-family: "resume_font";
  font-style: normal;
  font-weight: normal;
  speak: never;
 
  display: inline-block;
  text-decoration: inherit;
  width: 1em;
  margin-right: .2em;
  text-align: center;
  /* opacity: .8; */
 
  /* For safety - reset parent styles, that can break glyph codes*/
  font-feature-settings: normal;
  font-variant: normal;
  text-transform: none;
 
  /* fix buttons height, for twitter bootstrap */
  line-height: 1em;
 
  /* Animation center compensation - margins should be symmetric */
  /* remove if not needed */
  margin-left: .2em;
 
  /* you can be more comfortable with increased icons size */
  /* font-size: 120%; */
 
  /* Font smoothing. That was taken from TWBS */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
 
  /* Uncomment for 3D effect */
  /* text-shadow: 1px 1px 1px rgba(127, 127, 127, 0.3); */
}
 
.rsm-heart:before { content: '\e800'; } /* '' */
.rsm-heart-empty:before { content: '\e801'; } /* '' */
.rsm-th-list:before { content: '\e802'; } /* '' */
.rsm-down-open:before { content: '\e803'; } /* '' */
.rsm-stackoverflow:before { content: '\e804'; } /* '' */
.rsm-mobile:before { content: '\e805'; } /* '' */
.rsm-up-open:before { content: '\e806'; } /* '' */
.rsm-spin4:before { content: '\e834'; } /* '' */
.rsm-twitter:before { content: '\f099'; } /* '' */
.rsm-github:before { content: '\f09b'; } /* '' */
.rsm-magic:before { content: '\f0d0'; } /* '' */
.rsm-html5:before { content: '\f13b'; } /* '' */
.rsm-css3:before { content: '\f13c'; } /* '' */
.rsm-linkedin:before { content: '\f30c'; } /* '' */
* {
  padding: 0px;
  margin: 0;
  direction: rtl;
}
.pages {
  width: 100%;
  min-height: 100vh;
  background-color: red;
  padding-top: 3%;
}
.center {
  margin: 0 auto;
  text-align: center;
}
.topcenter {
  padding-top: 40vh;
}
.pages h1 {
  font-size: 100px;
}
.subtitle {
  font-size: 27px;
  color: gray;
  list-style: circle;
}
.subtitle li {
  display: inline-block;
  list-style: circle;
  margin-right: 10px;
}
.pages h2 {
  text-align: center;
  font-size: 70px;
  margin-bottom: 6vh;
  color: white;
}
.pages p {
  width: 45%;
  text-align: left;
  margin: 14px auto;
  font-size: 19px;
  color: rgb(27, 27, 27);
}

ul.socials li {
  font-size: 40px;
  margin: 20px 15px 15px 0;
  padding: 10px;
  display: inline-block;
}
ul.socials li a {

  transition: color 0.4s;
}
ul.socials li a:hover {
  color: red;
}

.cards li {
  width: 33%;

  display: inline-block;
}
.cards li div {
  width: 70%;
  height: 280px;
  margin: 0 auto;
  border-radius: 50px;
  margin-bottom: 40px;
  transition: background-color 0.2s;
  overflow: scroll;
}
.cards h5 {
  text-align: center;
  font-size: 30px;
  color: white;
}
.cards img {
  width: 64px;
  height: 64px;
  display: block;
  margin: 0 auto;
  padding: 15px 0;
}
.cards p {
  width: 80%;
  font-size: 20px;
  font-weight: lighter;
  color: rgb(8, 8, 8);
  text-align: center;
  line-height: 1.3em;
}
.cards div:hover {
  background-color: rgb(161, 161, 161) !important;
  color: black !important;
  cursor: pointer;
}
@media (max-width: 768px) {
  .cards li {
    width: 50%;
  }
}
@media (max-width: 450px) {
  .cards li {
    width: 100%;
  }
}

